    <section class="activity-list-wrap">
      <h1>‚òï„ÇÑ„ÇäÊñπ</h1>
      <div class="list-wrap">
        <ol>
          <li><kbd>"„Çµ„Ç§„Ç≥„É≠„ÇíÊäï„Åí„Çã"</kbd>„ÇíÊäº„Åô„ÄÇ</li>
          <li>„Çµ„Ç§„Ç≥„É≠„ÅåÂõû„ÇäÂßã„ÇÅ„Çã„ÄÇ</li>
